.time {
  font-size: 70px;
  font-family: HarmonyOS_Sans;
  user-select: none;
  font-weight: 400;
  text-shadow: 0 2px 6px rgba(0, 0, 0, 0.16);
  color: #fff;
  line-height: 70px;
  display: inline-block;
  text-align: center;
  time {
    display: inline-block;
    width: 84px;
    text-align: left;
  }
  .colon {
    vertical-align: calc(70px / 12);
  }
}
.date {
  font-size: 14px;
  line-height: 26px;
  opacity: 0.88;
  margin-top: -3px;
  text-shadow: 0 2px 4px rgb(0 0 0 / 16%);
  color: #fff;
  text-align: center;
  user-select: none;
  & > span {
    margin: 0 2px;
  }
}
