.main {
  display: flex;
  height: 100vh;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.main > section {
  position: relative;
  width: 100%;
  height: 100%;
}

.homePage {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
  flex-direction: column;

  .homePageHeaderWrap {
    height: 6vh;
    transition: .2s;
    position: relative;
  }
  .homePageDateWrap {
    text-align: center;
  }
  .homePageSearchWrap {
    text-align: center;
  }
  .homePageAppGridWrap {
    flex: 1 1 0%;
  }
  .homePageTipsWrap {
    background: linear-gradient(rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.5));
    padding-top: 20px;
    height: 60px;
    line-height: 40px;
  }
  .homePageBarWrap {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 12px;
    display: flex;
    align-items: center;
  }
}

@media screen and (max-width: 568px) {
  .homePage {
    .homePageHeaderWrap {
      height: 40px;
    }
  }
}
