.searchWrap {
  max-width: 600px;
  margin: 3vh auto 20px;
  padding: 0 26px;
  position: relative;
  :global {
    .ant-input-group {
      box-shadow: 0 0 10px 3px rgba(0,0,0,0.1);
      border-radius: 8px;
      .ant-input-group-addon {
        border: none;
        background-color: #fff;
      }
      .ant-input-borderless {
        background-color: #fff;
      }
    }
  }
}
