.apps {
  // pointer-events: none;
  max-width: 1146px;
  margin: 0 auto;
  padding: 16px 46px;
  height: 100%;
  .appItem {
    margin: 0 auto;
    text-align: center;
    display: block;
    color: #fff;
    background-color: rgba(0,0,0,0.8);
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 0 4px 16px 0 rgba(0,0,0,0.15);
    transform: translateY(0);
    border-radius: 20px;
    &:hover {
      transform: translateY(-4px);
    }
  }
}
